
.signup_container{
    padding-left: 16px;
    padding-right: 16px;
    padding-top: 140px;
    padding-bottom: 100px;
}

.signup_form{
    @media screen and(min-width:768px) {
        margin-left: auto;
        margin-right: auto;
        width: 380px;
    }
}

.signup_title{
    font-size: 40px;
    line-height: 1;
    padding-bottom: 50px;
    text-align: center;
}

.signup_input{
    padding-left: 16px;
    padding-top: 14px;
    padding-bottom: 14px;
    width: 100%;

    &:not(:last-child){
        margin-bottom: 50px;
    }

    // @media screen and(min-width:768px) {
    //     width: 380px;
    // }
}

.signup_select{
    padding-top: 43px;
    padding-bottom: 47px;

    @media screen and(min-width:768px) {
        padding-bottom: 50px;
    }
}

.signup_select_title{
    padding-bottom: 11px;
}

.signup_select_list{
    

}

.signup_select_list-item{
    position: relative;
}

.signup_select_list-input{
    -webkit-appearance: none;
    appearance: none;
    position: absolute;

   
}

.signup_select_list-input:checked + .signup_select_list-label::before{
    border-color: var(--secondary-color);
}

.signup_select_list-input:checked + .signup_select_list-label::after{
    opacity: 1;
}

.signup_select_list-label{
    padding-left: 34px;
    cursor: pointer;

    &:not(:last-child){
        padding-bottom: 7px;
    }
     &::before{
        content: "";
        display: block;
        width: 20px;
        height: 20px;
        border: 1px solid #D0CFCF;
        border-radius: 50%;
        position: absolute;
        top:2px;
        left: 0;
        z-index: 1;
    }
    &::after{
        content: "";
        display: block;
        width: 10px;
        height: 10px;
        background-color: var(--secondary-color);
        border-radius: 50%;
        opacity: 0;
        position: absolute;
        top:7px;
        left: 5px;
        z-index: 2;
    }
}

input[type=file]{
    display: none;
}

.signup_load_img{
    display: flex;
    align-items: center;
    padding-bottom: 50px;

    // @media screen and(min-width:768px) {
    //     width: 380px;
    // }
}

.signup_load_img-label{
    padding-left: 15px;
    padding-right: 15px;
    padding-top: 14px;
    padding-bottom: 14px;
    border: 1px solid var(--black-color);
    border-top-left-radius: 5px;
    border-bottom-left-radius: 5px;
}

.signup_load_img-input{
    padding-left: 16px;
    padding-top: 14px;
    padding-bottom: 14px;
    width: 100%;
    font-size: 16px;
    line-height: 1.6;
    color: #7E7E7E;
    background-color: var(--background-color);
    border: 1px solid #D0CFCF;
    border-top-right-radius: 5px;
    border-bottom-right-radius: 5px;
}

.signup_buttons{
    text-align: center;
}