
.header_block{
    background-color: var(--balack-header);
    height: 60px;
    width: 100%;
}

.nav{
    display: flex;
    padding-left: 16px;
    padding-right: 16px;
    padding-top: 13px;
    padding-bottom: 13px;

    @media screen and (min-width:768px){
        padding-left: 32px;
        padding-right: 32px;
        padding-top: 17px;
        padding-bottom: 17px;
        justify-content: space-between;
    }

    @media screen and(min-width:1024px) {
        padding-left: 60px;
        padding-right: 60px;
    }

}

.nav_logo{
    padding-right: 14px;
}

.nav_buttons{

}

.nav_buttons-item{

    &:not(:last-child){
        margin-right: 10px;
    }
}