.users_container{
    padding-top: 140px;
    padding-left: 16px;
    padding-right: 16px;
    padding-bottom: 140px;
    text-align: center;
}

.users_title{
    font-size: 40px;
    line-height: 1;
    text-align: center;
    padding-bottom: 50px;
}

.users_list{
    padding-bottom: 50px;

    @media screen and (min-width:768px) {
        display: flex;
        flex-wrap: wrap;
    }
}

.users_item{
    padding-left: 20px;
    padding-right: 20px;
    padding-top: 20px;
    padding-bottom: 20px;
    background-color: var(--white-color);
    border-radius: 10px;
    width: 328px;

    @media screen and (max-width:767px) {
        &:not(:last-child){
            margin-bottom: 20px;
        }   
    }

    @media screen and (min-width:768px) and(max-width:1023px) {
        width: 344px;

        &:not(:nth-child(2n)){
            margin-right: 16px;
        }

        &:nth-last-child(n+3){           
            margin-bottom: 16px;
        }   
    }

    @media screen and(min-width:1024px) {
        width: 282px;
        &:not(:nth-child(3n)){
            margin-right: 29px;
        }
        &:nth-last-child(n+4){
            margin-bottom: 29px;
    }
}
}

.users_img{
    display: flex;
    justify-content: center;
    padding-bottom: 20px;
}

.users_img-item{
    width: 70px;
    height: 70px;
    border-radius: 50px;
}

.users_name{
    padding-bottom: 20px;
}

.users_description{

}

.users_contacts{
    display: flex;
    flex-direction: column;
}