.container {
    width: 100%;
    margin-left: auto;
    margin-right: auto;

    @media screen and (min-width: 360px) {
        max-width: 360px;
    }

    @media screen and (min-width: 768px) {
        max-width: 768px;
    }

    @media screen and (min-width: 1024px) {
        max-width: 1024px;
    }
}