.hero{
    padding-top: 40px;
    padding-left: 16px;
    padding-right: 16px;
    background-size: cover;
    background-position: center;
    height: 500px;
    background-image: url('../../picture/img/pexels-alexandr-podvalny-360px.jpeg');

    @media screen and (min-width: 768px) {
        padding-top: 89px;
        background-image: url('../../picture/img/pexels-alexandr-podvalny-768px.jpeg');
    }
    @media screen and (min-width: 1024px) {
        padding-top: 164px;
        height: 650px;
        background-image: url('../../picture/img/pexels-alexandr-podvalny-1024px.jpeg');
    }
}

.hero_container{
    color:var(--white-color);
    text-align: center;
    
    @media screen and(min-width:768px) {
        max-width: 380px;
        margin-left: auto;
        margin-right: auto;
    }
}

.hero_title{
    font-size: 40px;
    line-height: 1;
    padding-bottom: 21px;
}

.hero_description{
    padding-bottom: 32px;
}